import PubSub from "pubsub-js";
import getWeatherBackground from "./weatherBackgrounds";
import gsap from "gsap";
const animations = () => {
  const weatherInfoAnimation = () => {
    const tl = gsap
      .timeline()
      .from(".weather-info__weather-icon", {
        duration: 2,
        delay: 1,
        ease: "power1.out",
        x: 500,
        y: -500,
      })
      .from(
        ".weather-info__weather",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.2"
      )
      .from(
        ".weather-info__temperature",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.2"
      )
      .from(
        ".weather-info__city",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.2"
      )
      .from(
        ".weather-info__date",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.2"
      );

    return tl;
  };

  const dailyWeatherForecastAnimation = () => {
    const tl = gsap
      .timeline()
      .from("#day1", {
        duration: 2,
        ease: "power1.out",
        x: 500,
        y: -500,
      })
      .from(
        "#day2",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.1"
      )
      .from(
        "#day3",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.1"
      )
      .from(
        "#day4",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -500,
        },
        "<0.1"
      )
      .from(
        "#day5",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -800,
        },
        "<0.1"
      )
      .from(
        "#day6",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -800,
        },
        "<0.1"
      )
      .from(
        "#day7",
        {
          duration: 2,
          ease: "power1.out",
          x: 500,
          y: -800,
        },
        "<0.1"
      );
  };

  const backgroundChangeAnimation = () => {
    const tl = gsap.timeline().to("body", {
      backgroundImage: `url(${getWeatherBackground({
        weatherCode: 804,
        date: Date.now(),
      })})`,
      duration: 1.5,
    });

    return tl;
  };
  backgroundChangeAnimation();
  dailyWeatherForecastAnimation();
  weatherInfoAnimation();
};

export default animations;
